/* Basic modal styling */
.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
  }
  
  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 90%;
    max-width: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 15px;
  }
  
  .modal-header h2 {
    margin: 0;
  }
  
  .modal-close {
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .chat-messages {
    max-height: 300px;
    overflow-y: auto;
    margin-bottom: 20px;
  }
  
  .chat-input {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .chat-input input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
  }
  
  .chat-input button {
    padding: 10px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .chat-input button:hover {
    background-color: #45a049;
  }
  
  /* Media Queries for mobile responsiveness */
  @media (max-width: 600px) {
    .modal-content {
      width: 95%;
    }
  
    .chat-input button {
      padding: 12px;
    }
  }
  
  /* Style for the "Open Chat" button */
  .open-chat-btn {
    position: fixed;
    top: 20px;
    left: 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    z-index: 9999;
  }
  
  .open-chat-btn:hover {
    background-color: #45a049;
  }
  